.card {
  width: 100%;
  /* background-color: rgb(245, 143, 48); */
  box-shadow: 0 0 5px #79797938;
  display: flex;
  flex-direction: column;
  transition: 0.5s;
  /* border-radius: 0.5em; */
}

.card__thumbnail {
  width: 100%;
  aspect-ratio: 1 / 1;
  /* filter: brightness(0); */
  object-fit: contain;
  position: absolute;
  top: 0;
  left: 0;
}

.card__info {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
}

.card__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card__info__name {
  font-weight: 100;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1em;
  display: flex;
  align-items: center;
  gap: 3px;
}

.card__info__name__verified {
  color: var(--accent);
  /* color: #10db65; */
  font-size: 0.8em;
  margin-block-start: 1px;
}

.card__info__date {
  display: flex;
  gap: 5px;
}

.card__info__date span {
  font-size: 0.8em;
  color: #969696;
}

.card__info__site {
  font-size: 0.8em;
  text-transform: capitalize;
  color: var(--accent-2);
}

.card__info__buttons {
  display: flex;
}

.card__info__link {
  padding: 10px;
  justify-content: center;
  align-items: center;
  background-color: var(--accent);
  text-decoration: none;
  /* border-radius: 0.5em; */
  font-size: 0.8em;
  display: flex;
  gap: 5px;
  flex-grow: 1;
  color: #fff;
}

.bg-dark .card__info__link {
  color: #222;
}

.card__info__link svg {
  font-size: 1.5em;
}
